.App {
  display: flex;
  width: 100%;
  height:  calc(100% - 10px);
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}

.App-header{
      width: 100%;
      height: 74px;
      background-color: #61dafb;
  }
  .App-login{
    width: 70%;
    height: 50%;
    background-color: blue;
  }



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

